body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
}

.thinker-container {
    position: relative;
    margin: 0 auto;
    width: 250px;
    overflow: hidden;
}

.hat {
    position: absolute;
    left: 120px;
    top: -10px;
}

.App {
    text-align: center;
}

.App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
    height: 80px;
}

.App-header {
    background-color: #222;
    height: 150px;
    padding: 20px;
    color: white;
}

.App-intro {
    font-size: large;
}

@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

.mra-navbar-nav {
    -ms-flex-direction: row;
        flex-direction: row;
}

.mra-navbar-nav .nav-item {
    padding: .5rem;
}

.draw {
    margin: 5px;
}

.colors {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
}

.color {
    margin: 5px;
    width: 30px;
    height: 30px;
    cursor: pointer;
}

.grid {
    width: 5px;
    height: 5px;
    cursor: pointer;
    border: 0 solid rgb(238, 238, 238);
    -webkit-border-image: initial;
         -o-border-image: initial;
            border-image: initial;
    background: rgb(255, 255, 255);
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.gridRow {
    float: left;
}

.draw {
    display: block;
    width: 322px;
    float: left;
    border: 1px solid rgb(238, 238, 238);
    -webkit-border-image: initial;
         -o-border-image: initial;
            border-image: initial;
}


